@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap');

.render {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  background-color: #ededed;
}

header {
  /* border-bottom: 3px solid #823967; */
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
  max-width: 100vw;
  max-height: 10vh;
  padding-bottom: 10px;




}

.heading {
  font-size: 200%;
  /* border: 1px solid black; */
  margin: 20px 20px;
  font-weight: 700;
  display: flex;
  flex-direction: row;
}

#head {
  margin-left: 10px;
  position: relative;
  top: 3px;
  font-family: 'Roboto', sans-serif;
  color: #396782;
}


.App-link {
  color: #61dafb;
}

.add_board {
  height: 6vh;
  width: 14vw;
  margin: 20px;
  border-radius: 50px;
  border: 2px solid#396782;
  background-color: transparent;
  color: #396782;
  font-size: 17px;

}

.add_board:hover {
  background-color: #396782;
  color: #ebebeb;
  font-weight: 700;

}